import { BaseHandler } from './base/BaseHandler.js'
import Vue from 'vue';

const statisticsTimer = 'ObjectiveStatics:statisticsTimer'; //顶部时间提交人数组件
const deps = [
  statisticsTimer
];

/**
 * 
 * 
 * @class StatisticsTitleHandler
 * @extends {BaseHandler}
 */
class StatisticsTitleHandler extends BaseHandler {
  constructor(context, container,option) {
    super(context, container,option);
    this.setName("StatisticsTitleHandler");
  }

  preRender() {
    //如果app.smallVue存在，要先destroy掉
    this.myContainer = this.mContainer.querySelector(".com_layout_header");
    if (this.myContainer) {
      this.myContainer.innerHTML = '<timer :data="data" :i18n="i18n"></timer>';
      return true;
    }
    return false;
  }

  render(app, data) {
    let allowRender = this.preRender();
    allowRender && this._startRender(app, data);
  }

  _startRender(app, data) {
    let i18n = $.extend(true, {}, this.mContext.langProperties);
    if (Midware && Midware.componentRequire) {
      app.titleVue && app.titleVue.$destroy();
      Midware.componentRequire(deps).then((comps) => {
        app.titleVue = new Vue({
          el: this.myContainer,
          data: {
            i18n: i18n,
            data: data
          },
          components: {
            'timer': comps[statisticsTimer]
          }
        });
      });
    }
  }
}


export { StatisticsTitleHandler };


