import {userIdToName} from '../../utils/ClassroomUtil.js'

const __context = {
  '_service_': {}
};

/**
 * 处理器基类
 * 
 * @class BaseHandler
 */
class BaseHandler {
  constructor(context, container,option) {
    this.name = null;
    this.mContext = context || __context;
    this.mContainer = container;
    this.mOption = option;
    this.$mContainer = $(container);
    this.v = null;
    this.env = "ppt";
    this._init();
  }

  _init(){
    if(this.mOption){
      if(this.mOption.env){
        this.env = this.mOption.env;
      }
    }
  }

  setName(n) {
    this.name = n;
  }

  /**
   * 送花
   * 
   * @param {any} users
   * 
   * @memberOf BaseHandler
   */
  onSendFlower(users) {
    this.mContext.fireStuffEvent("FLOWER", users);
    if (window.PresenterFlowerTip) {
      window.PresenterFlowerTip(userIdToName(users));
    }
  }

  /**
   * 关闭查看解析
   * 
   * 
   * @memberOf BaseHandler
   */
  onCloseAnalysis() {

  }
}

export { BaseHandler };


