import Config from './Config.js';
import { getQuestionType } from '../utils/util.js'

/**
 * 统计处理路由器
 * 
 * @class Router
 */
class Router {

  constructor(app, container, context, option) {
    this.mApp = app;
    this.mContext = context;
    this.mContainer = container;
    this.mConfig = Config(context, container, option);
    this.handlers = {};
    this.curHandler = null;
  }

  router(data, statisticClass) {
    if (statisticClass === "D") {
      return;
    }
    let _questionType = getQuestionType(data, statisticClass);
    let handler = this.findHandler(_questionType, statisticClass);
    if (handler) {
      this.curHandler = handler;
      if (!this.handlers.hasOwnProperty(handler.name)) {
        this.handlers[handler.name] = handler;
      }
      handler.handle(this.mApp, data, statisticClass);
    }
  }

  /**
   * 寻找合适的处理器
   * 
   * @param {any} qt
   * @param {any} clazz
   * @returns
   * 
   * @memberOf Router
   */
  findHandler(qt, clazz) {
    let _config = this.mConfig;
    let _special = _config['special'];
    let _clazz = _config['clazz'];
    if (_special.hasOwnProperty(qt)) {
      return _special[qt];
    } else if (_clazz.hasOwnProperty(clazz)) {
      return _clazz[clazz];
    }
    return null;
  }
}

export default Router;


