import { BaseHandler } from './base/BaseHandler.js'
import { getCurrentStudents } from '../utils/ClassroomUtil.js';
import Vue from 'vue';

const statisticsA = 'ObjectiveStatics:statisticsA'; //A类统计组件
const deps = [
  statisticsA
];

/**
 * 
 * 
 * @class StatisticsAHandler
 * @extends {BaseHandler}
 */
class StatisticsAHandler extends BaseHandler {
  constructor(context, container, option) {
    super(context, container, option);
    this.setName("StatisticsAHandler");
    this.analysisContent = "";
    this.isShowAnalysis = false;
  }

  preHandle() {
    //如果app.smallVue存在，要先destroy掉
    this.myContainer = this.mContainer.querySelector(".statistics-area");
    if (this.myContainer) {
      this.myContainer.innerHTML = require("../template/StatisticsA.html");
      this.mContainer.parentNode.classList.add("pull_down");
      return true;
    }
    return false;
  }

  /**
   * 查看解析
   * 
   * @param {any} content
   * 
   * @memberOf StatisticsAHandler
   */
  showAnalysis(content) {
    this.analysisContent = content;
    this.isShowAnalysis = true;
  //暂不实现
  }

  showAnswer() {
    this.showCorrectAnswer = true;
    this.v.$data.showCorrectAnswer = this.showCorrectAnswer;
  }

  showRightAnswer(correctAnswerDisplayCtrl) {
    this.correctAnswerDisplayCtrl = correctAnswerDisplayCtrl;
    this.v.$data.correctAnswerDisplayCtrl = this.correctAnswerDisplayCtrl;
  }

  handle(app, data) {
    let allowRender = this.preHandle();
    allowRender && this._startRender(app, data);
  }

  _startRender(app, data) {
    this.correctAnswerDisplayCtrl = {
      showCorrectAnswer: false,
      correctAnswer: []
    };
    let i18n = $.extend(true, {}, this.mContext.langProperties);
    if (Midware && Midware.componentRequire) {
      app.smallVue && app.smallVue.$destroy();

      data.value.currentStudents = data.value.currentStudents?data.value.currentStudents: getCurrentStudents();

      Midware.componentRequire(deps).then((comps) => {
        console.log(this);
        app.smallVue = new Vue({
          el: this.myContainer,
          data: {
            i18n: i18n,
            data: data,
            env: this.env,
            correctAnswerDisplayCtrl:this.correctAnswerDisplayCtrl
          },
          components: {
            'statisticsA': comps[statisticsA]
          }
        });
        app.smallVue.$on("FLOWER", this.onSendFlower.bind(this));
        app.smallVue.$on("CLOSE_ANALYSIS", this.onCloseAnalysis.bind(this));
        this.v = app.smallVue;
      });
    }
  }
}


export { StatisticsAHandler };


