import $ from '$'

let utils = window.ClassroomUtils;
let _  = window._;

/**
 * 用户id转换为学生姓名
 */
function userIdToName(users) {
  let _users = [];
  if (utils && utils.getCurrentStudents && users instanceof Array) {
    _users = $.extend(true, [], users);
    var students = utils.getCurrentStudents();
    if (students instanceof Array) {
      for (let i = 0, ilen = _users.length; i < ilen; i++) {
        for (let j = 0, jlen = students.length; j < jlen; j++) {
          if (students[j]['studentId'] === _users[i]) {
            _users[i] = students[j]['studentName'];
            break;
          }
        }
      }
    }
  }
  return _users;
}

/**
 * 获取当前所有学生
 * 
 * @returns
 */
function getCurrentStudents(){
    let students = [];
    if (utils && utils.getCurrentStudents) {
        students = utils.getCurrentStudents();
    }
    return students;
}

export { userIdToName,getCurrentStudents }


