import { CompoundHandler } from '../handle/CompoundHandler.js';
import { RoleReadingHandler } from '../handle/RoleReadingHandler.js';
import { SentenceHandler } from '../handle/SentenceHandler.js';
import { SectionsHandler } from '../handle/SectionsHandler.js';
import { StatisticsAHandler } from '../handle/StatisticsAHandler.js';
import { StatisticsBHandler } from '../handle/StatisticsBHandler.js';
import { StatisticsCHandler } from '../handle/StatisticsCHandler.js';
import { StatisticsEHandler } from '../handle/StatisticsEHandler.js';

/**
 * 路由配置
 * 
 * @export
 * @param {any} context
 * @param {any} container
 * @returns
 */
export default function (context, container, option) {
  return {
    "special": {
      "Compound": new CompoundHandler(context, container, option),
      "ReadingComprehension": new CompoundHandler(context, container, option),
      "ChineseCharacterDictation": new CompoundHandler(context, container,option),
      "EnglishDictation": new CompoundHandler(context, container,option),
      "rolereading": new RoleReadingHandler(context, container, option),
      "sentence": new SentenceHandler(context, container, option),
      "sections": new SectionsHandler(context, container, option)
    },
    "clazz": {
      "A": new StatisticsAHandler(context, container, option),
      "B": new StatisticsBHandler(context, container, option),
      "C": new StatisticsCHandler(context, container, option),
      "E": new StatisticsEHandler(context, container, option)
    }
  }
}


