import Vue from 'vue';
import Vuex from 'vuex';
import HandleRouter from '../router/HandleRouter.js';
import { StatisticsTitleHandler } from '../handle/StatisticsTitleHandler.js';

/**
 * 统计组件启动器
 * 
 * @export
 * @class App
 */
export default class App {
  constructor(container, context, option) {
    this.titleVue = null; //用于统计顶部，时间和提交人数，渲染在com_layout_header节点上
    this.smallVue = null; //用于A,B,C,E类统计，渲染在statistics-area节点上
    this.bigVue = null; //用于F,G类统计,渲染在statistics-overview节点上 
    this.mContext = context;
    this.mContainer = container;
    this.mTitleHandler = new StatisticsTitleHandler(context, container, option);
    this.mHandleRouter = new HandleRouter(this, container, context, option);
  }

  handle(data, statisticClass) {
    let _data1 = $.extend(true, {}, data);
    let _data2 = $.extend(true, {}, data);
    this.renderTitle(_data1);
    this.mHandleRouter.router(_data2, statisticClass);
  }

  renderTitle(data) {
    this.mTitleHandler.render(this, data);
  }

  gotoIndex() {
    let _handler = this.mHandleRouter.curHandler;
    _handler && _handler['gotoIndex'] && _handler['gotoIndex'].apply(_handler, arguments);
  }

  /**
   * 查看解析
   * 
   * 
   * @memberOf App
   */
  showAnalysis() {
    let _handler = this.mHandleRouter.curHandler;
    _handler && _handler['showAnalysis'] && _handler['showAnalysis'].apply(_handler, arguments);
  }

  /**
   * 公布结果
   * 
   * 
   * @memberOf App
   */
  showAnswer() {
    let _handler = this.mHandleRouter.curHandler;
    _handler && _handler['showAnswer'] && _handler['showAnswer'].apply(_handler, arguments);
  }

  /**
   * 公布结果
   * 用于口头出题和截图发题
   * 
   * @memberOf App
   */
  showRightAnswer(correctAnswerDisplayCtrl){
    let _handler = this.mHandleRouter.curHandler;
    _handler && _handler['showRightAnswer'] && _handler['showRightAnswer'].call(_handler, correctAnswerDisplayCtrl);
  }

  /**
   * 重置vue
   * 
   * 
   * @memberOf App
   */
  reset() {}
}


