import { BaseHandler } from './base/BaseHandler.js'
import { getCurrentStudents } from '../utils/ClassroomUtil.js';

const statisticsB = 'ObjectiveStatics:statisticsB'; //B类统计组件
const deps = [
  statisticsB
];

/**
 * 
 * @class StatisticsBHandler
 * @extends {BaseHandler}
 */
class StatisticsBHandler extends BaseHandler {
  constructor(context, container,option) {
    super(context, container,option);
    this.setName("StatisticsBHandler");
  }

  preHandle() {
    this.clickItemIndex = {};
    this.myContainer = this.mContainer.querySelector(".statistics-area");
    if (this.myContainer) {
      this.myContainer.innerHTML = require("../template/StatisticsB.html");
      this.mContainer.parentNode.classList.add("pull_down");
      return true;
    }
    return false;

  }

  gotoIndex(index, direction) {
    let temp = {"index":index+1};
    if(direction){
      temp["type"] = direction;
    }
    this.v.$data.clickitemindex = temp;
  }

  handle(app, data) {
    let allowRender = this.preHandle();
    allowRender && this._startRender(app, data);
  }

  _startRender(app, data) {
    let i18n = $.extend(true, {}, this.mContext.langProperties);
    if (Midware && Midware.componentRequire) {
      app.smallVue && app.smallVue.$destroy();

      data.value.currentStudents = data.value.currentStudents?data.value.currentStudents: getCurrentStudents();

      Midware.componentRequire(deps).then((comps) => {
        app.smallVue = new Vue({
          el: this.myContainer,
          data: {
            i18n: i18n,
            data: data,
            clickitemindex:this.clickItemIndex,
            env:this.env
          },
          components: {
            'statisticsB': comps[statisticsB]
          }
        });
        app.smallVue.$on("FLOWER",this.onSendFlower.bind(this));
        this.v = app.smallVue;
      });
    }
  }
}


export { StatisticsBHandler };


